/**
 * @callback cacheGet
 * 
 * @param {String} url
 * @return {Boolean|String}  URL  false String
 */

/**
 * @callback cacheSet
 * 
 * @param {String} url
 * @param {String} html
 */

const defaults = require('../../defaults/render-cache');

/**
 * @class React  URL (: )
 */
class KootReactRenderCache {
    /**
     * @param {Object} [options={}]
     * @param {number} [options.maxAge] 
     * @param {number} [options.maxCount]  URL 
     * @param {cacheGet} [options.get] , maxAge  maxCount 
     * @param {cacheSet} [options.set] , maxAge  maxCount 
     */
    constructor(options = {}) {
        if (options === true) options = {};
        const {
            maxAge = defaults.maxAge,
            maxCount = defaults.maxCount
        } = options;

        this.list = new Map();
        this.cachedUrls = [];
        this.maxAge = maxAge;
        this.maxCount = maxCount;
        this.customGet = options.get;
        this.customSet = options.set;

        // console.log({
        //     maxAge: this.maxAge,
        //     maxCount: this.maxCount
        // });
    }

    /**
     * 
     * @param {String} url
     * @return {Boolean|String}  URL  false String
     */
    get(url) {
        if (typeof this.customGet === 'function') return this.customGet(url);

        //  false
        if (!this.list.has(url)) return false;

        const { html, time } = this.list.get(url);

        // 
        if (Date.now() - time < this.maxAge) {
            // console.log('')
            // console.log(`cached result: ${url} | ${time} | ${Date.now() - time}`)
            // console.log('')
            return html;
        }

        //  false
        this.remove(url);
        return false;
    }

    /**
     * 
     * @param {String} url
     * @param {String} html
     */
    set(url, html) {
        if (typeof this.customSet === 'function')
            return this.customSet(url, html);

        //  URL 
        // 
        if (!this.list.has(url) && this.cachedUrls.length >= this.maxCount) {
            // console.log('')
            // console.log(`cached count out: ${url} | ${this.cachedUrls.length}`)
            // console.log('')
            this.remove(this.cachedUrls[0]);
            // console.log('new list', this.cachedUrls)
        }

        // 
        this.cachedUrls.push(url);
        this.list.set(url, {
            html,
            time: Date.now()
        });
    }

    /**
     * 
     * @param {*} url
     */
    remove(url) {
        this.list.delete(url);
        this.cachedUrls.splice(this.cachedUrls.indexOf(url), 1);
    }
}

module.exports = KootReactRenderCache;
