const path = require('path');

const getDirDistPublicFoldername = require('./get-dir-dist-public-foldername');

/**
 * 
 *  includes ()
 * @param {String} dist 
 * @returns {String}
 */
module.exports = dist => {
    if (!result) {
        result = path.resolve(dist, getDirDistPublicFoldername());
    }

    return result;
};

let result;
